<script>
    import HeaderLogo from '../Layout/HeaderLogo'
    import { mapState,mapMutations } from "vuex";
    export default {
        computed: mapState({
            cartList: state => state.cartList,
            totalPrices: state => state.totalPrices,
        }),
        components:{
            HeaderLogo
        },
        methods:{
            ...mapMutations(['saveCartList','getCartList','deleteCartShop']),
            deleteshop(id){
                this.deleteCartShop(id)
            },
            reduceCart(item){
                if(item.count>1){
                    this.saveCartList({id:item.id,count:-1});
                }
            },
            addCart(shop){
                let {id,name,price} = shop;
                this.saveCartList({id,name,price,count:1});
            }
        }
    };
